$bg-color: #fff;
$text-color: #333;
$primary-color: #007bff;
$secondary-color: #6c757d;

.App {
  text-align: center;

}

 .title {
    color: $primary-color;
  }
.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

