:root {
    --main-red: #be3110;
    --main-clear: #b0cbce;
    --main-blueL: #3f787e;
    --main-blue: #11565d;
    --main-grayL: #292a2a;
    --main-gray: #151718;
    --main-dark: #080d10;
  }

nav{
    height: 100px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: var(--main-dark);


    margin: 0;
    padding: 0;
    border-top: 5px solid white;
    border-bottom: 5px solid white;
    
  
    a {
        text-decoration: none;
        padding: 20px 0 20px 0;
        width: 30%;

        .nav-links {
            color: var(--main-clear);

            text-transform: uppercase;
            transition: all ease-in-out 0.4s;

            .icons {
                color:rgb(29, 29, 29);

                font-size: 15px;
                animation: nav-spin infinite 9.2s linear;

            }
            .iconsR {
                color:rgb(29, 29, 29);

                font-size: 15px;
                animation: nav-spinR infinite 9s linear;
            }

        }
    }

    .nav-links:hover {
        color: white;   
        text-shadow: 1px 5px 5px rgba(143, 202, 153, 0.5);
        font-size: larger;

        .icons {

            color:rgb(53, 53, 53);
            font-size: 16px;
            animation: nav-spin infinite 3s linear;
        }
        .iconsR {

            color:rgb(53, 53, 53);
            font-size: 16px;
            animation: nav-spinR infinite 3.2s linear;
        }

        
    }

    .active {
        font-weight: bolder;
        font-size: 22px;
        transition: all ease-in-out 0.8s;
        border-bottom: 15px solid white;
        border-top: 15px solid white;
        margin: -20px 0 -20px 0;

        .icons {
            color:rgb(73, 73, 73);
            font-size: 17px;
            animation: nav-spin infinite 6.2s linear;
        }
        .iconsR {
            color:rgb(73, 73, 73);
            font-size: 17px;
            animation: nav-spinR infinite 6s linear;
        }

        
    }
    .active:hover {
        font-weight: lighter;
        transition: all ease-in-out 0.8s;
        text-shadow: 1px 5px 7px rgba(250, 248, 248, 0.9);
        .nav-links {
            color:black;    
        }
        

        .icons {
            color:rgb(43, 43, 43);
            animation: nav-spin infinite 3s linear;
            transition: all ease-in-out 0.8s;
        }
        .iconsR {
            color:rgb(43, 43, 43);
            animation: nav-spinR infinite 3.2s linear;
            transition: all ease-in-out 0.8s;
        }
    }
      
}
@keyframes nav-spin {
    from {
      transform: rotate(0deg);
    }
    to {
      transform: rotate(360deg);
    }
}

@keyframes nav-spinR {
    from {
      transform: rotate(0deg);
    }
    to {
      transform: rotate(-360deg);
    }
}


@media (max-width: 428px) {
    nav {
        border: none;       
        width: 100%;

        a {
            .nav-links {
                font-size: 15px;

                .icons {
                    font-size: 12px;
                }
                .iconsR {
                    font-size: 12px;
                }
            }
        }

        .nav-links:hover {
            text-shadow: none;
            font-size: medium;
    
            .icons {
    
                color:rgb(255, 255, 255);
                font-size: 16px;
                animation: nav-spin infinite 3s linear;
            }
            .iconsR {
    
                color:rgb(53, 53, 53);
                font-size: 16px;
                animation: nav-spinR infinite 3.2s linear;
            }
    
            
        }
        .active {
            font-size: 16px;
            border-bottom: 0 solid white;
            border-top: 0 solid white;
            margin: 0;
            font-weight: 100;
            .icons {
                font-size: 10px;
            }
            .iconsR {
                font-size: 10px;
            }
    
            
        }
    }
}
@media (max-width: 520px) {
    nav {      
        .icons{
            display: none;
        }
        .iconsR {
            display: none;

        }
    }
}